import { useState } from "react";
import pic from "../Media/Warframe0040.jpg";
import styles from "../css/Sidebar.module.scss";
const Sidebar = ({ setpage, className }) => {
  const [visible, setvisible] = useState(true);
  return (
    <div className={className}>
      <div className={styles.padding}></div>
      <div
        className={styles.btn}
        onClick={() =>
          setpage({
            home: true,
            projects: false,
            about: false,
            resume: false,
          })
        }
      >
        Home
      </div>
      <div
        className={styles.btn}
        onClick={() =>
          setpage({
            home: false,
            projects: true,
            about: false,
            resume: false,
          })
        }
      >
        Projects
      </div>
      <div
        className={styles.btn}
        onClick={() =>
          setpage({
            home: false,
            projects: false,
            about: true,
            resume: false,
          })
        }
      >
        About
      </div>
      <div
        className={styles.btn}
        onClick={() =>
          setpage({
            home: false,
            projects: false,
            about: false,
            resume: true,
          })
        }
      >
        Resume
      </div>
    </div>
  );
};

export default Sidebar;
