import { useState, useEffectt } from "react";
import Home from "../components/Home";
import ImageSlider from "../components/ImageSlider";
import About from "../components/About";
import Resume from "../components/Resume";
import styles from "../css/Content.module.scss";
import "../css/transitions.scss";
import { CSSTransition } from "react-transition-group";

const Content = ({ page, className }) => {
  return (
    <div className={className}>
      <CSSTransition
        in={page.home}
        appear={true}
        timeout={750}
        classNames="fade"
        unmountOnExit
      >
        <Home />
      </CSSTransition>
      <CSSTransition
        in={page.projects}
        appear={true}
        timeout={750}
        classNames="fade"
        unmountOnExit
      >
        <ImageSlider />
      </CSSTransition>
      <CSSTransition
        in={page.about}
        appear={true}
        timeout={750}
        classNames="fade"
        unmountOnExit
      >
        <About />
      </CSSTransition>
      <CSSTransition
        in={page.resume}
        appear={true}
        timeout={750}
        classNames="fade"
        unmountOnExit
      >
        <Resume />
      </CSSTransition>
    </div>
  );
};

export default Content;
