import styles from "./App.module.scss";
import { useState } from "react";
import Sidebar from "./components/Sidebar";
import Content from "./components/Content";
import pic from "./Media/Warframe0040.jpg";
import { FaBars } from "react-icons/fa";
import { Spring } from "react-spring/renderprops";
import { CSSTransition } from "react-transition-group";
import { animated } from "react-spring/renderprops-universal";
import styled from "styled-components";

function App() {
  const [page, setpage] = useState({
    home: true,
    projects: false,
    about: false,
    resume: false,
  });
  const [visible, setvisible] = useState(false);

  let sidebarClassname = visible ? styles.sidebar : styles["sidebar-open"];
  let contentClassname = visible ? styles.content : styles["content-open"];
  let iconClassname = visible ? styles.icon : styles["icon-open"];
  return (
    <>
      <FaBars className={iconClassname} onClick={() => setvisible(!visible)} />
      {/* <img
        src={pic}
        style={{ width: 25, height: 25, cursor: "pointer", position: "fixed" }}
        onClick={() => setvisible(!visible)}
      /> */}
      <Sidebar className={sidebarClassname} setpage={setpage} />
      <Content className={contentClassname} page={page} />
    </>
  );
}

export default App;
