import { useState, useEffect } from "react";
import styles from "../css/ImageSlider.module.scss";
import pic from "../Media/Wow.jpg";
import pic2 from "../Media/Screenshot_200523_000.jpg";
import pic3 from "../Media/47.png";
import pic4 from "../Media/45.png";
import pic5 from "../Media/46.png";

const ImageSlider = ({ setpage }) => {
  const [img, setimg] = useState([pic, pic2, pic3, pic4, pic5]);
  const [idx, setidx] = useState(0);
  const [visible, setvisible] = useState(true);
  useEffect(() => {
    return () => {};
  }, []);
  if (visible) {
    return (
      <div className={styles.img}>
        <h1>ImageSlider</h1>
        <div>
          <button onClick={() => setidx(idx < 1 ? img.length - 1 : idx - 1)}>
            previous
          </button>
          <img
            style={{
              width: 300,
              height: 300,
              cursor: "pointer",
            }}
            src={img[idx]}
            onClick={() => setidx(idx > img.length - 2 ? 0 : idx + 1)}
          />
          <button onClick={() => setidx(idx > img.length - 2 ? 0 : idx + 1)}>
            next
          </button>
          <button onClick={() => setvisible(!visible)}>hide</button>
          {/* <button onClick={() => setpage("Home")}>Switch to Home</button> */}
        </div>
      </div>
    );
  } else {
    return (
      <div className={styles.img}>
        <button onClick={() => setvisible(!visible)}>show</button>
        {/* <button onClick={() => setpage("Home")}>Switch to Home</button> */}
      </div>
    );
  }
};

export default ImageSlider;
